.button {
    background-color: transparent;
    border: none;
}

.button:hover {
    cursor: pointer;
}

.button--complete {
    transition: 1s;
}

.button--complete:hover {
    color: hsl(0, 0%, 100%);
    background-color: hsl(120, 100%, 35%);
    border-radius: 15px;
}

.button--done {
    background-color: hsl(120, 100%, 25%);
    color: hsl(0, 0%, 100%);
    border-radius: 15px;
}

.button--done:hover {
    background-color: hsl(120, 100%, 35%);
}

.button--delete {
    transition: 1s;
}

.button--delete:hover {
    background-color: hsl(0, 100%, 50%);
    color: white;
    border-radius: 15px;
}

.button--delete:active {
    color: hsl(0, 0%, 0%);
}

.section__buttons {
    display: flex;
    gap: 20px;
}

@media (max-width: 767px) {
    .section__buttons {
        flex-direction: column;
        align-items: center;
        margin-bottom: 20px;
    }
}

.button--selectAll {
    padding: 5px;
    color: hsl(180, 100%, 25%);
    transition: 1s;
}

.button--selectAll:hover {
    color: hsl(180, 100%, 35%);
}

.button--selectAll:active {
    color: hsl(0, 0%, 0%);
}

.button--selectAll:disabled {
    color: hsl(0, 0%, 87%);
    cursor: auto;
}

.button--hideDone {
    min-width: 127px;
    padding: 5px;
    color: hsl(180, 100%, 25%);
    transition: 1s;
}

.button--hideDone:hover {
    color: hsl(180, 100%, 35%);
}

.button--hideDone:disabled {
    color: hsl(0, 0%, 87%);
    cursor: auto;
}